@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans+Devanagari);
@import url(https://fonts.googleapis.com/css?family=Libre+Franklin);
:root {
  --russian-violet: #320e3b;
  --bittersweet: #f87060;
  --platinum: #e7e7e7;
  --viridian-green: #32908f; /* 50, 144, 143 */
  --ebony: #545c52;
}
.navbar {
  background-color: #e7e7e7;
  background-color: var(--platinum);
  line-height: 50px;
  height: 75px;
  display: flex;
  width:75%;
  justify-content: center;
}
.navbar-btn {
  cursor: pointer;
  background-color: transparent;
  border: 0;
  font-weight: 700;
  transition: all 0.1s;
  color: #545c52;
  color: var(--ebony)
}
.navbar-btn:hover {
  color: #320e3b;
  color: var(--russian-violet);
}
.hero {
  padding: 3em 3em 7em 25%;
  background-color: #320e3b;
  background-color: var(--russian-violet);
  color: #e7e7e7;
  color: var(--platinum);
}
.cards {
  padding: 1em;
  display: flex;
  margin: auto;
  justify-content:space-evenly;
  width: 50%;
}
.card-background{
  border-radius: 5px;
  position: absolute;
  width:100%;
  height:100%;
  background: linear-gradient(rgba(50, 144, 143,1.0), rgba(50, 144, 143,0.9), rgba(50, 144, 134,0.6))
}
.card-image {
  box-shadow: 0 2px 2px #545c52;
  box-shadow: 0 2px 2px var(--ebony);
  border-radius: 5px;
  transition: all 0.1s;
  cursor: pointer;
  background-color: #32908f;
  background-color: var(--viridian-green);
  background-size: auto 100%;
  background-position: center;
  position: relative;
  width:250px;
  height:300px;
}
.card-image:hover {
  box-shadow: 0 10px 15px #545c52;
  box-shadow: 0 10px 15px var(--ebony);
}
.card-image:active {
  box-shadow: 0 2px 5px #545c52;
  box-shadow: 0 2px 5px var(--ebony);
}
.card-text {
  color: #e7e7e7;
  color: var(--platinum);
  text-align: center;
}

body {
  font-family: "IBM Plex Sans Devanagari", serif;
  margin: 0;
  color: #545c52;
  color: var(--ebony);
  background-color: #e7e7e7;
  background-color: #e7e7e7;
  background-color: var(--platinum);
}

h1, h3 {
  font-family: "Libre Franklin", sans-serif;
}

button {
  background-color: #f87060;
  background-color: var(--bittersweet);
  padding: 0.5em 3em;
  transition: all 0.1s;
  cursor: pointer;
  color: #320e3b;
  color: var(--russian-violet);
  border-radius: 15px;
  border-style: solid;
}
button,
button:hover {
  border-color: #f87060;
  border-color: var(--bittersweet);
}
button:hover {
  background-color: rgba(0,0,0,0);
  color: #f87060;
  color: var(--bittersweet);
  border-width: 2px;
}

@media screen and (max-width: 1920px) {
  .cards {
    width: 100%;
  }
}

